/* App.css */
.orange-bg {
  background-color: orange;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Optional: Set height for centering vertically */
}

table {
  border-collapse: collapse;
  width: 100%;
}

th, td {
  border: 1px solid black;
  padding: 8px; /* Adjust as needed */
  text-align: left;
}

th {
  background-color: #f2f2f2;
}
